.bubbleContainer {
  position: relative;
  width: 100px;
  height: 300px;
  margin-top: 100px;
}
.bubble{
    position: absolute;
    top: 90%;
    left: 50%;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.8);
    -webkit-filter: blur(2px);
            filter: blur(2px);
  }
.bubble::after{
    position: absolute;
    content: '';
    height: 5px;
    width: 5px;
    top: 10%;
    left: 9%;
    border-radius: 50%;
    background-color: rgb(255, 255, 255, 0.5);
    /* border: 1px solid rgba(255, 255, 255, 0.5); */
  }
  .small{
    width: 13px;
    height: 13px;
    left: 50%;
    -webkit-animation: flow-s 2s linear infinite;
            animation: flow-s 2s linear infinite;
  }
  .small-l{
    width: 13px;
    height: 13px;
    left: 43%;
    -webkit-animation: flow-s-l 0.9s linear infinite;
            animation: flow-s-l 0.9s linear infinite;
  }
  .s-medium{
    width: 20px;
    height: 20px;
    left: 43%;
    -webkit-animation: flow-s 1.3s linear infinite;
            animation: flow-s 1.3s linear infinite;
  }
  .medium{
    width: 25px;
    height: 25px;
    left: 45%;
    -webkit-animation: flow-m 1s linear infinite;
            animation: flow-m 1s linear infinite;
  }
  .large{
    width: 30px;
    height: 30px;
    left: 57%;
    -webkit-animation: flow-l 1.1s linear infinite;
            animation: flow-l 1.1s linear infinite;
  }
.bubble-slow{
    position: absolute;
    top: 53%;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.8);
  }
  .small-slow{
    width: 10px;
